<template>
  <div id="shopping-item" style="display: flex">
      {{ item.name }}
      <i v-if="menuIsOpen" class="material-icons" id="clear-icon" @click="deleteItem">clear</i>
      <i v-if="menuIsOpen" class="material-icons" id="edit-icon" @click="updateItem">create</i>
  </div>
</template>

<script>
import ItemService from '../../api-service/ItemService'
export default {
  name: "ShoppingItem",
  props: {
    listKey: Number
  },
  methods: {
    updateItem() {
    },
    async deleteItem() {
      ItemService.deleteItem(this.key, await this.$auth.getTokenSilently())
        .then(res => {
          if ((Array.isArray(res.data) && res.data[0] !== 'ERROR') || !Array.isArray(res.data)) {
            this.$store.commit('deleteItem', this.item);
          } else {
            this.$store.commit('showGenericError');
          }
        });
    }
  },
  computed: {
    item() {
      return this.$store.getters.getItemById(this.key);
    },
    key() {
      return this.$vnode.key;
    },
    menuIsOpen() {
      console.log(this.listKey)
      return this.$store.getters.getOpenMenuStatusById(this.listKey);
    },
  }
}
</script>

<style scoped>
#clear-icon {
  margin-left: 16px;
  color:rgb(185, 2, 2);
}
#edit-icon {
  margin-left: 16px;
  color:rgb(2, 0, 138);
}
#shopping-item {
  margin: 3px;
  padding: 4px;
  padding-top: 3px;
  padding-left: 12px;
  padding-right: 7px;
  margin-bottom: 7px;
  border: 1px solid rgb(255, 145, 0);
  border-radius: 20px;
  box-shadow: 0 0 4px rgb(255, 145, 0);
  /* font-family:monospace; */
  text-align: left;
  overflow-wrap: break-word;
}
</style>